
.food__choose {
  font-size: 0;
}
.food__choose .food__subtract,
  .food__choose .food__add,
  .food__choose .food__num,
  .food__choose .animateBall {
    display: inline-block;
    line-height: 2.4rem;
    font-size: 2.4rem;
    height: 2.4rem;
    width: 2.4rem;
    vertical-align: middle;
    color: #15c9ff;
}
.food__choose .food__add {
    z-index: 10;
}
.food__choose .food__num {
    width: 2.4rem;
    text-align: center;
    font-size: 1rem;
}
@media only screen and (max-width: 350px) {
.food__choose .food__num {
      width: 1.4rem;
}
}
.food__choose .animateBall {
    position: fixed;
    height: 1.7rem;
    width: 1.7rem;
    border-radius: 50%;
    background-color: red;
}
.food__choose .move-enter-active,
  .food__choose .move-leave-active {
    transition: all 0.5s linear;
}
.food__choose .move-enter,
  .food__choose .move-leave-active {
    -webkit-transform: translateX(4.8rem) rotate(180deg);
            transform: translateX(4.8rem) rotate(180deg);
    opacity: 0;
}
.food__choose .fade-enter-active,
  .food__choose .fade-leave-active {
    transition: all 0.6s linear;
}
.food__choose .fade-enter,
  .food__choose .fade-leave-active {
    opacity: 0;
}



